import { Component, OnInit,Input ,Output, EventEmitter } from '@angular/core';
import { FormGroup, FormControl, Validators, FormBuilder } from '@angular/forms';
import { CommonPaymentService } from '../../serivces/common-payment.service';

@Component({
  selector: 'lib-payment-bank-details',
  templateUrl: './payment-bank-details.component.html',
  styleUrls: ['./payment-bank-details.component.scss']
})
export class PaymentBankDetailsComponent implements OnInit {


  @Input() paymentData:any;
  countryName: any;
  paymentType: any;
  paymentForm!: FormGroup;
  paymentForm1!: FormGroup;
  testForm!:FormGroup;
  companyNameSelected:boolean=false;
  addressline1:any;
  addressline2:any;
  addressline3:any;
  surname:any;
  name:any;
  county:any;
  postcode:any;
  customerName:any;

  @Input() paymentTypeS:any;
  @Output() payEmitter:EventEmitter<any> = new EventEmitter<any>();
  userData: any;
  constructor(
    private fb: FormBuilder,private commonService:CommonPaymentService
  ) { }

  ngOnInit(): void {

    if(this.paymentTypeS=='PAD'){
      this.paymentType=1;
      this.commonService.getCountryName().subscribe(
        (response: any) => {
            this.countryName=response.map((country:any) => country.name.common);
        });
    }
    else if(this.paymentTypeS=='Bank transfer'){
      this.paymentType=2;
      this.buildPaymetForm();
    }
    else if(this.paymentTypeS=='ACH'){
      this.paymentType=3;
    }
    else if(this.paymentTypeS=='SEPA'){
      this.paymentType=4;
    }
    this.companyNameSelected = this.paymentData?.customerDetails?.customerType != 'customer'
    console.log(this.paymentData);
    this.addressline1 = this.paymentData?.customerDetails?.addressline1 ? this.paymentData?.customerDetails?.addressline1 : this.paymentData.addressline1;
    this.addressline2 = this.paymentData?.customerDetails?.addressline2 ? this.paymentData?.customerDetails?.addressline2 : this.paymentData.addressline2;
    this.addressline3 = this.paymentData?.customerDetails?.addressline3 ? this.paymentData?.customerDetails?.addressline3 : this.paymentData.addressline3;
    this.surname = this.paymentData?.customerDetails?.surname ? this.paymentData.customerDetails.surname : this.paymentData.lastName
    this.name = this.paymentData?.customerDetails?.name ? this.paymentData.customerDetails.name : this.paymentData.firstName
    this.postcode = this.paymentData?.customerDetails?.postcode ? this.paymentData.customerDetails.postcode : this.paymentData.postcode
    this.county = this.paymentData?.customerDetails?.county ? this.paymentData.customerDetails.county : this.paymentData.county;
    this.customerName = this.paymentData?.customerDetails?.customerName ? this.paymentData.customerDetails.customerName :this.paymentData.customerName;
  }


  buildPaymetForm() {
    if( this.paymentType==1){
      this.paymentForm = this.fb.group({
        firstName: ['',[Validators.required] ],
        lastName: ['', [Validators.required]],
        email: ['', [Validators.required,Validators.email] ],
        country: ['', [Validators.required]],
        institutionNo: ['', [Validators.required]],
        transitNo: ['', [Validators.required]],
        accountNo: ['', [Validators.required,] ],
      });

    }
    else if( this.paymentType==2){
      let invoiceNo;
      if(this.paymentData.invoiceDetails?.invoiceNumber){
        invoiceNo = this.paymentData.invoiceDetails?.invoiceNumber ? "Invoice no : "+this.paymentData.invoiceDetails.invoiceNumber : '';
      }else{
        invoiceNo = 'Opportunity no: ' + this.paymentData.opportunityId
      }
      this.paymentForm = this.fb.group({
      instantpayment: this.fb.group({
          country_code:'GB',
          currency:this.paymentData.currencyCode ? this.paymentData.currencyCode : 'GBP',
          payment:this.fb.group({
            description:invoiceNo,
            amount:[0, [Validators.required]],
            app_fee:"1",

          }),
          customer:this.fb.group({
            commusoftId:0,
            addressline1: this.addressline1,
            addressline2: this.addressline2+ ((this.addressline2 && this.addressline3)?", ":'')+this.addressline3,
            companyname:"",
            surname: this.surname,
            firstname: this.name,
            postcode:this.postcode,
            phonenumber:"",
            region:this.county,
            email: [this.paymentData?.customerDetails?.emailId ? this.paymentData?.customerDetails?.emailId :'',[Validators.required, Validators.email]]
          }),
          bank:this.fb.group({
            firstname:[this.customerName, [Validators.required]],
            lastname:[""],
            account_number:['', [Validators.required]],
            iban:"",
            account_type:"",
            bank_code:"",
            branch_code:["", [Validators.required]],

          })

        }),
      });
    }
    else if( this.paymentType==3){
      let invoiceNo;
      if(this.paymentData.invoiceDetails?.invoiceNumber){
        invoiceNo = this.paymentData.invoiceDetails?.invoiceNumber ? "Invoice no : "+this.paymentData.invoiceDetails.invoiceNumber : '';
      }else{
        invoiceNo = 'Opportunity no: ' + this.paymentData.opportunityId
      }
      this.paymentForm = this.fb.group({
        directdebit: this.fb.group({
            tablepkid: [],
            tabletype:'invoice',
            country_code:'US',
            bank_mandate_ref:[''],
            currency: this.paymentData.currencyCode,
            scheme:"ach",
            payment:this.fb.group({
              description:invoiceNo,
              amount:[0, [Validators.required]],
            }),
            customer:this.fb.group({
              commusoftId:0,
              addressline1: this.addressline1,
              addressline2: this.addressline2+ ((this.addressline2 && this.addressline3)?", ":'')+this.addressline3,
              companyname:"",
              surname: this.surname,
              firstname: this.name,
              postcode:this.postcode,
              phonenumber:"",
              region:this.county,
              email: [this.paymentData.customerDetails.emailId,[Validators.required, Validators.email]],
              language:""
            }),
            bank:this.fb.group({
              firstname:[this.customerName, [Validators.required]],
              lastname:[""],
              account_number:['', [Validators.required]],
              iban:"",
              account_type:"",
              bank_code:"",
              branch_code:["", [Validators.required]],
            })
          }),
        });
    }
    else if( this.paymentType==4){
      this.paymentForm = this.fb.group({
        firstName: ['',[Validators.required] ],
        lastName: ['', [Validators.required]],
        email: ['', [Validators.required,Validators.email] ],
        creditorIdentifier: ['', [Validators.required]],
        internationalBankAccountNo: ['', [Validators.required]],
        accountNo: ['', [Validators.required,] ],
        billingAddress1: ['', [Validators.required]],
        billingAddress2: ['', [Validators.required]],
      });
    }
  }

  companyNameClick(){
    this.companyNameSelected=!this.companyNameSelected;
    this.paymentForm.patchValue({'instantpayment':{'bank':{'firstname':''}}});
    this.paymentForm.patchValue({'directdebit':{'bank':{'lastname':''}}});
    this.paymentForm.get('directdebit.bank.lastname')?.setValue(this.companyNameSelected ? this.customerName : (this.surname ? this.surname : this.customerName));
  }
  
  keypress(){ 
    this.payEmitter.emit(this.paymentForm);
  }
  branch_codeFormat(el:any){
    this.paymentForm.patchValue({'instantpayment':{'bank':{'branch_code':el.target.value.replace(/[^0-9]/g, '').replace(/(\d{2})(?=\d)/g, "$1-")}}});
    this.keypress()
  }

}
