import { Component, OnInit ,Input ,Output, EventEmitter } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { CreditCardValidators } from 'angular-cc-library';
import { CommonPaymentService } from '../../serivces/common-payment.service';

@Component({
  selector: 'lib-payment-card-details',
  templateUrl: './payment-card-details.component.html',
  styleUrls: ['./payment-card-details.component.scss']
})
export class PaymentCardDetailsComponent implements OnInit {

  @Output() payEmitter:EventEmitter<any> = new EventEmitter<any>();

  userData: any;
  storedCards: any=[];
  cardType: any='';
  @Input() cardPaymentData:any;
  // @Input() storedCards:any;
  cardSelectedType: any;
  card:boolean=false;
  container: any;
  cardwidth: Number = 350;
  messages: any = {validDate: 'valid\ndate', monthYear: 'mm/yy'};  //Strings for translation
  placeholders: any = {number: '•••• •••• •••• ••••', name: 'Full Name', expiry: '••/••', cvc: '•••'} // Placeholders for rendered fields
  masks: any;
  formatting: boolean = true;
  debug: boolean = true; // If true, will log helpful messages for setting up Card
  creditForm!: FormGroup;
  summary: any;
  companyNameSelected:boolean=false;
  isCardNumberLength: boolean=true;
  cardLogo: any='';
  cardImg: string = "";
  storedCardData:any;
  paymentStoredDetials:any;
  portalName:any;

    constructor(
    private fb: FormBuilder,
    private commomPaymentService: CommonPaymentService
  ) { this.cardPaymentData = this.cardPaymentData;
    this.commomPaymentService.paymentDetails$.subscribe((response:any)=>{
      this.cardPaymentData = response;
    })
    this.card = false;
    this.buildCreditFormS();}

  ngOnInit(): void {
  
    this.payEmitter.emit(true);

    let invoiceAddressesId = this.cardPaymentData.invoiceDetails?.invoiceAddressId ? this.cardPaymentData.invoiceDetails.invoiceAddressId : this.cardPaymentData.invoiceAddressNo;
    this.portalName = this.commomPaymentService.apiUrl?.portal;
    this.commomPaymentService.paymentStoredCard$.subscribe((stored:any)=>{
      this.paymentStoredDetials = stored;
    });
    console.log('invaddress0',this.paymentStoredDetials);
    console.log('cardpaymentdetal',this.cardPaymentData);
    if(this.commomPaymentService.apiUrl?.apiUrl != ''){
      if(this.paymentStoredDetials == null || this.paymentStoredDetials == undefined){
        this.commomPaymentService.getStorecard(invoiceAddressesId).subscribe((res:any)=>{
          if(res && res.records){
            this.storedCards = res.records;
          }else{
            this.storedCards = res;
          }
          this.commomPaymentService.setPaymentStoredCard(this.storedCards);
          console.log('cardDetails',this.storedCards);
        });
      }else{
        this.storedCards = this.paymentStoredDetials;
        console.log('cardDetailsafter',this.storedCards);
      } 
    }

  }


  dropdown(val:any){
    this.card =false;
    this.payEmitter.emit(false);
    if(val.value == 'Set up new debit or credit card'){
      this.card = true;
      if(this.portalName != 'AcceptProposal'){
        this.buildCreditForms();
      }else{
        this.buildCreditFormS();
      }
    }else{
        if(this.storedCards.storedCardDetails.length == 1){
          this.cardSelected(this.storedCards.storedCardDetails[0].id,'ts')
        }
      this.card = false;
    }
    // this.translate.get("Use existing credit card").subscribe(translations => {
    //   if(val.value== translations){
    //     this.card=true
    //     if(this.storedCards.length == 1){
    //       this.cardSelected(this.storedCards[0].id,'ts')
    //     }
    //   }
    //   else{
    //     this.card=false
    //     this.buildCreditForms()
    //   }
    // })

  }

  buildCreditForms(){
    let date = new Date().toISOString().slice(0, 10);
    let invoiceNo;
    if(this.cardPaymentData.invoiceDetails?.invoiceNumber){
      invoiceNo = this.cardPaymentData.invoiceDetails?.invoiceNumber ? "Invoice no : "+this.cardPaymentData.invoiceDetails.invoiceNumber : '';
    }else{
      invoiceNo = 'Opportunity no: ' + this.cardPaymentData.opportunityId
    }
    let paymentId;
    if(this.cardPaymentData?.cardCharges.length != 0){
      paymentId = this.cardPaymentData.cardCharges[this.commomPaymentService.cardCharges].paymentMethodId
    }
    this.creditForm = this.fb.group({
      payment: this.fb.group({
      'cardCharge': [this.cardPaymentData?.cardCharges[0].charge ? this.cardPaymentData.cardCharges[0].charge : this.cardPaymentData.paymentCharge[0].charge ],//index UI needed
      'cardName': [this.cardPaymentData?.customerDetails?.customerName ? this.cardPaymentData.customerDetails.customerName : this.cardPaymentData.customerName, [Validators.required] ],
      'cardNumber': ['', [Validators.required, CreditCardValidators.validateCCNumber]],
      'cardCvc': ['', [Validators.required, Validators.minLength(3), Validators.maxLength(4)]],
      'paymentDate': [date],
      'description': [invoiceNo],
      'isportal': [true ],
      'reference': [invoiceNo ],
      'email': [this.cardPaymentData?.customerDetails?.emailId ? this.cardPaymentData.customerDetails.emailId : '',[Validators.required, Validators.email]],
      'expiry': ['', [Validators.required, CreditCardValidators.validateExpDate]],

      "paymentMethod":[paymentId ? paymentId : ''],
      "dontSendToAccounts":[''],
      })

    });
    this.placeholders = {name: this.cardPaymentData?.customerDetails?.customerName ? this.cardPaymentData.customerDetails.customerName:'Full Name'};
  }

  buildCreditFormS(){
    let date = new Date().toISOString().slice(0, 10);
    let invoiceNo;
    if(this.cardPaymentData?.invoiceDetails?.invoiceNumber){
      invoiceNo = this.cardPaymentData?.invoiceDetails?.invoiceNumber ? "Invoice no : "+this.cardPaymentData.invoiceDetails.invoiceNumber : '';
    }else{
      invoiceNo = 'Opportunity no: ' + this.cardPaymentData.opportunityId
    }
    let paymentId;
    if(this.cardPaymentData?.cardCharges.length != 0){
      paymentId = this.cardPaymentData.cardCharges[this.commomPaymentService.cardCharges].paymentMethodId
    }
    this.creditForm = this.fb.group({
      'amount': [this.cardPaymentData?.showDeposit ? this.cardPaymentData?.showDeposit : ''],
      'cardCharge': [this.cardPaymentData?.cardCharges[0].charge ? this.cardPaymentData.cardCharges[0].charge : this.cardPaymentData.paymentCharge[0].charge ],//index UI needed
      'cardName': [this.cardPaymentData?.customerDetails?.customerName ? this.cardPaymentData.customerDetails.customerName : this.cardPaymentData.customerName, [Validators.required] ],
      'cardNumber': ['', [Validators.required, CreditCardValidators.validateCCNumber]],
      'cardCvc': ['', [Validators.required, Validators.minLength(3), Validators.maxLength(4)]],
      'paymentDate': [date],
      'description': [invoiceNo],
      'isportal': [true ],
      'reference': [invoiceNo ],
      'email': [this.cardPaymentData?.customerDetails?.emailId ? this.cardPaymentData.customerDetails.emailId : '',[Validators.required, Validators.email]],
      'expiry': ['', [Validators.required, CreditCardValidators.validateExpDate]],

      "paymentMethod":[paymentId ? paymentId : ''],
      "dontSendToAccounts":[''],
    });
    this.placeholders = {name: this.cardPaymentData?.customerDetails?.customerName ? this.cardPaymentData.customerDetails.customerName:'Full Name'};
  }

  companyNameClick(){
    this.companyNameSelected=!this.companyNameSelected;
    this.creditForm.patchValue({'payment':{'cardName':''}});
  }
  keypress(){
    this.payEmitter.emit(this.creditForm);
  }
  setCardType(){
    this.cardLogo=this.detectCardType(this.creditForm.value.payment.cardNumber.replace(/\s/g, ""))
    var cardStatus = this.creditForm.get('payment.cardNumber')
    if(this.isCardNumberLength && cardStatus?.status == "INVALID")this.isCardNumberLength=false;
    if(!this.isCardNumberLength && cardStatus?.status == "VALID")this.isCardNumberLength=true;
    this.payEmitter.emit(this.creditForm);
    this.cardImg = "assets/paymentType/"+this.cardLogo+".svg"
  }
  defaultCard(){
    this.cardImg = "assets/paymentType/card.svg"
  }
  errorHandler() {
    this.cardLogo = 'card';
 }
  cardSelected(val:any,from:string){
    if(from=='html'){
      val=val.value
    }
    this.cardSelectedType = from;
    this.cardType=val.toString();

    var intArr = this.cardType.split(",");
    intArr.forEach((element:any) => {
      const tagsData = this.storedCards.storedCardDetails.find((x:any) => x['id'] == element);
      this.cardLogo = tagsData.card_type;
    });

    let date = new Date().toISOString().slice(0, 10);
    let invoiceNumber = this.cardPaymentData.invoiceDetails?.invoiceNumber ? "Invoice no : #"+this.cardPaymentData.invoiceDetails.invoiceNumber : '';
    this.creditForm = this.fb.group({
        'paymentDate' : [date, [Validators.required] ],
        'description' : [invoiceNumber],
        'reference' : [invoiceNumber ],
        'paymentMethod' : [this.cardPaymentData.cardCharges[this.commomPaymentService.cardCharges].paymentMethodId, [Validators.required]],
        'storedCard' : [val, [Validators.required]],
        'cardName' : [this.cardPaymentData.customerDetails.customerName, [Validators.required]],
        'amount' : [],
        'cardCharge' : [this.cardPaymentData.cardCharges[this.commomPaymentService.cardCharges].charge, ],
        'isPortal' : [true],
      });
      setTimeout(() => {
        this.payEmitter.emit(this.creditForm);
      }, 100);
}

detectCardType(number:number) {
  var re :any = {
      electron: /^(4026|417500|4405|4508|4844|4913|4917)\d+$/,
      maestro: /^(5018|5020|5038|5612|5893|6304|6759|6761|6762|6763|0604|6390)\d+$/,
      dankort: /^(5019)\d+$/,
      interpayment: /^(636)\d+$/,
      unionpay: /^(62|88)\d+$/,
      'Visa': /^4[0-9]{12}(?:[0-9]{3})?$/,
      'MasterCard': /^5[1-5][0-9]{14}$/,
      'American Express': /^3[47][0-9]{13}$/,
      diners: /^3(?:0[0-5]|[68][0-9])[0-9]{11}$/,
      discover: /^6(?:011|5[0-9]{2})[0-9]{12}$/,
      jcb: /^(?:2131|1800|35\d{3})\d{11}$/
  }

  for(var key in re) {
      if(re[key].test(number)) {
        return key
      }
  }
  return false;
}


}
